      start() {
        console.log("re-rendered")
        var self = this;

        self.listen();

        function whichTransitionEvent() {
          var t;
          var el = document.createElement('fakeelement');
          var transitions = {
            'animation': 'animationend',
            'OAnimation': 'oAnimationEnd',
            'MozAnimation': 'animationend',
            'WebkitAnimation': 'webkitAnimationEnd'
          }

          for (t in transitions) {
            if (el.style[t] !== undefined) {
              return transitions[t];
            }
          }
        }

        /* Listen for a transition! */
        var transitionEvent = whichTransitionEvent();
        transitionEvent && self.$el.addEventListener(transitionEvent, function () {
          console.log('Transition complete!  This is the callback, no library needed!');
          Array.from(self.$el.querySelectorAll(".animated")).forEach(
            val => {
              // debugger
              val.classList.remove("zoomInUp")
            }
          )
        });
      }
    },


    created: function () {
      this.idDisplay = this.$route.params.idDisplay;
      this.idCine = this.$route.params.idCine;
      this.totalPantallas = this.$route.params.totalPantallas

